.d-flex {
  position: relative; }

.work-projects a {
  font-size: 50px;
  font-weight: 900;
  font-style: italic;
  color: white;
  text-decoration: none;
  margin: 0 45px; }

.work-title {
  color: white; }

.logos {
  position: absolute;
  bottom: -180px;
  left: -180px; }

/*media queries*/
@media (max-width: 576px) {
  .about-me {
    color: white;
    font-size: 12px;
    font-weight: 300;
    max-width: 800px;
    margin: 0 auto; }
  .main-title {
    font-size: 20px; }
  .barcode.active-about-mobile {
    margin-bottom: -245px; } }

@media (min-width: 576px) and (max-width: 768px) {
  .active-nilima span {
    position: absolute;
    bottom: 0px;
    margin: 0 auto;
    width: 100%;
    color: white;
    opacity: 0.1;
    font-weight: 900;
    text-transform: uppercase;
    font-style: italic;
    font-size: 13.5vw;
    letter-spacing: 50px;
    margin-bottom: -40px;
    left: 0px; } }

.nav-links {
  margin: 0; }
  .nav-links span {
    position: relative;
    text-decoration: none;
    padding-bottom: 8px; }
  .nav-links span:before, .nav-links span:after {
    content: '';
    position: absolute;
    bottom: 2px;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #0077ff; }
  .nav-links span:before {
    opacity: 0;
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-transition: opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s;
    transition: transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275); }
  .nav-links span:after {
    opacity: 0;
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
    -webkit-transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
    transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275); }
  .nav-links span:hover:before, .nav-links span:hover:after, .nav-links span:focus:before, .nav-links span:focus:after {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  .nav-links span:hover:before, .nav-links span:focus:before {
    -webkit-transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
    transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275); }
  .nav-links span:hover:after, .nav-links span:focus:after {
    -webkit-transition: opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s 0.2s;
    transition: transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275); }

.nav-links span {
  display: inline-block;
  margin: 0 20px; }
